import { Injectable, EventEmitter } from '@angular/core';
import { Contact } from './contact.model';
// import { MOCKCONTACTS } from './MOCKCONTACTS';
import { Subject } from 'rxjs';
import { HttpClient, HttpHeaders } from '@angular/common/http';

@Injectable({ providedIn: 'root'})

export class ContactService {
  contactListChangedEvent = new Subject<Contact[]>();
  // contactChangedEvent = new EventEmitter<Contact[]>();
  // contactSelectedEvent = new EventEmitter<Contact>();
  contacts: Contact[] = [];
  maxContactId: number;

  constructor(private http: HttpClient) { 
    this.fetchContacts();
    this.maxContactId = this.getMaxId();
  }

  fetchContacts(){
    return this.http
    .get<Contact[]>(
      'https://samueltwagner-cms.firebaseio.com/contacts.json'
    )
    .subscribe((contacts: Contact[]) => {
      this.contacts = contacts;
      this.maxContactId = this.getMaxId();
      this.contacts.sort((a, b) => (a.name < b.name) ? 1 : (a.name > b.name) ? -1 : 0);
      this.contactListChangedEvent.next(this.contacts.slice());
      },
    (error: any) => {
      console.log(error);
      }
    )
  }

  storeContacts() {
    this.contacts = JSON.parse(JSON.stringify(this.contacts));
    const header = new HttpHeaders({'Content-Type': 'json'});
    this.http
      .put('https://samueltwagner-cms.firebaseio.com/contacts.json', this.contacts, {headers: header})
      .subscribe((returnedContacts: Contact[]) => {
        this.contactListChangedEvent.next(returnedContacts.slice());
      });
  }

  getContact(id: string): Contact {
    for (const contact of this.contacts){
      if (contact.id === id) {
        return contact;
      }
    }
    return null;
  }

  getContacts(): Contact[] {
    return this.contacts.slice();
  }

  getMaxId(): number {
    let maxId = 0;
    for (let contact of this.contacts) {
      let currentId = +contact.id;
        if (currentId > maxId) {
          maxId = currentId;
        }
        return maxId;
    }
  }

  addContact(newContact: Contact){
    if (!newContact) {
      return;
    }
    else {
      this.maxContactId++;
      newContact.id = String(this.maxContactId);
      this.contacts.push(newContact);
      this.storeContacts();
    }
  }

  updateContact(originalContact: Contact, newContact: Contact){
    if (!originalContact  ||  !newContact){
      return;
    }
    const pos = this.contacts.indexOf(originalContact);
    if ( pos < 0 ){
      console.log("No negative indexes");
      return;
    }
    newContact.id = originalContact.id;
    this.contacts[pos] = newContact;
    this.storeContacts();
  }
  

  deleteContact(contact: Contact) {
    if (contact === null || contact === undefined) {
      return;
    }
    const pos = this.contacts.indexOf(contact);
    if (pos < 0) {
        return;
      }
      this.contacts.splice(pos, 1);
      this.storeContacts();
  }
}
